import React from "react";
import { Box } from "@mui/material";
import * as Yup from "yup";
import GenericForm from "app/formComponents/GenericForm";
import appStore from "app/mobxStore/AppStore";

interface Props {
  data?: NewsLetter;
  reloadData?: () => void;
}

interface NewsLetter {
  id?: number;
  title: string;
  description: string;
  date: string;
  template: string;
}

const NewsLetterProperties: React.FC<Props> = ({ data, reloadData }) => {
  const defaultValues: NewsLetter = {
    title: "",
    description: "",
    date: "",
    template: "",
  };

  const initialValues: NewsLetter = {
    ...defaultValues,
    ...data,
  };

  const fields = [
    { name: "title", label: "Title", type: "string" },
    { name: "description", label: "Description", type: "textArea", column: 1 },
    { name: "date", label: "Date", type: "date" },
    { name: "template", label: "Template", type: "htmlEditor", column: 1 },
  ];

  const validationSchema = Yup.object({
    title: Yup.string().required("Title is required"),
    description: Yup.string().required("Description is required"),
    date: Yup.string().required("Date is required"),
    template: Yup.string().required("Template is required"),
  });

  const handleOnSubmit = (values: NewsLetter) => {
    appStore.showToast("Saved", "success");
    reloadData?.();
  };

  return (
    <Box display="flex" flexDirection="column" sx={{ height: "100%", backgroundColor: "white", mt: "5px" }}>
      <GenericForm
        fields={fields}
        initialValues={initialValues}
        validationSchema={validationSchema}
        onSubmit={handleOnSubmit}
        columns={2}
        showSaveButton={true}
        readOnly={false}
        showHeaderComponent={false}
      />
    </Box>
  );
};

export default NewsLetterProperties;
