import React, { useEffect, useState } from "react";
import { Box, Typography } from "@mui/material";
import { HeaderBox, InfoBox } from "app/appComponents/HeaderBox";
import DataGrid from "app/formComponents/DataGrid";
import { Split } from "app/formComponents/styles/Split";
import TabBar from "app/formComponents/TabBar";
import NewsLetterProperties from "./NewsLetterProperties";
import newslettersData from "app/data/newsletters.json";

const columns: any = [
  { id: "id", label: "ID", type: "number" },
  { id: "title", label: "Title", type: "string" },
  { id: "date", label: "Date", type: "date" },
];

const NewsLetters: React.FC = () => {
  const [selectedTab, setSelectedTab] = useState(0);
  const [newsletters, setNewsletters] = useState<any[]>([]);
  const [currentObj, setCurrentObj] = useState<any>(null);

  useEffect(() => {
    setNewsletters(newslettersData as any);
    setCurrentObj((newslettersData as any)[0]);
  }, []);

  const handleTabChange = (_event: React.ChangeEvent<{}>, newValue: number) => {
    setSelectedTab(newValue);
  };

  const handleSelection = (id: number) => {
    if (id === null) return;
    const selected = newsletters.find((item) => item.id === id);
    if (selected) setCurrentObj(selected);
  };

  const renderTab = (tabIndex: number) => {
    switch (tabIndex) {
      case 0:
      default:
        return <NewsLetterProperties data={currentObj} />;
    }
  };

  return (
    <div
      style={{
        backgroundColor: "#ffffff",
        height: "calc(100vh - 95px)",
        maxHeight: "100%",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        width: "100%",
      }}
    >
      <Box sx={{ width: "100%", borderRadius: "4px", height: "100%" }}>
        <Box
          sx={{
            height: "100%",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            borderBottom: "1px solid #ccc",
          }}
        >
          <Split
            left={
              <div
                style={{
                  paddingRight: "5px",
                  height: "100%",
                  display: "flex",
                  flexDirection: "column",
                }}
              >
                <HeaderBox style={{ height: "60px" }}>
                  <InfoBox>
                    <Typography variant="overline" sx={{ fontSize: 15, ml: "16px" }}>
                      {"News Letters"}
                    </Typography>
                  </InfoBox>
                </HeaderBox>
                <Box sx={{ flex: 1, overflow: "hidden" }}>
                  <DataGrid
                    tasks={newsletters}
                    columns={columns}
                    onSelection={handleSelection}
                    defaultHeight="100%"
                  />
                </Box>
              </div>
            }
            right={
              <div
                style={{
                  marginRight: "5px",
                  height: "100%",
                  display: "flex",
                  flexDirection: "column",
                  marginLeft: "4px",
                }}
              >
                <TabBar value={selectedTab} onChange={handleTabChange} tabs={["Properties"]} />
                <Box sx={{ height: "100%", overflow: "hidden", mt: "5px" }}>
                  {renderTab(selectedTab)}
                </Box>
              </div>
            }
          />
        </Box>
      </Box>
    </div>
  );
};

export default NewsLetters;
